.about-product {

    &__section {
        @include container--content; }

    &__title {
        @include gbackground(100%);
        display: inline-block;
        color: $white;
        text-transform: uppercase;
        font-size: 7.2rem;
        font-weight: 900;
        padding: 0px 20px;
        margin-bottom: 25px; }

    // start of 1st row of products
    &__products-1 {
        display: flex;
        width: 100%;
        justify-content: space-between;
        margin-bottom: 36px; }

    &__gum {
        display: flex;
        &-img {
            width: 165px;
            height: 354px; }
        &-caption {
            display: inline-block;
            width: 162px;
            margin-top: 45px;
            margin-left: 70px;
            padding: 10px 0; } }



    &__che {
        align-self: baseline;
        padding-top: 55px;
        display: flex; }

    &__sticker {
        align-self: center; }

    //start of 2nd row or products
    &__products-2 {
        display: flex;
        align-items: baseline;
        justify-content: space-between; }

    &__box {}

    &__clips {}
    // /product rows

    // repeatable classes elements in rows

    &__arrow-caption {
        font-size: 1.8rem;
        color: $text; }

    // all span captions must have this
    //  // display: inline-block
    //  // width: 162px

    // &__svg-wrapper (delete)

    &__svg {
        display: block; } }

